	export default {
		data() {
			return {
				avatarUrl:'',
				nickName:'',
				id:'',
				flag:false
			}
		},
		onLoad() {
			//判断用户是否授权

            uni.getSetting({
             success(res) {
                console.log("授权：",res);
               if (!res.authSetting['scope.userInfo']) {
				  console.log("未授权");
               } else {
                    //用户已经授权过了
                    console.log("当前已授权");
               }
             }
           })

		},
		methods: {
			//微信授权登录获取
			appLoginWx(){

				var _this = this
				_this.flag = true
					uni.getProvider({
					  service: 'oauth',
					  success: function (res) {
					  console.log(res)
					  if (~res.provider.indexOf('weixin')) {
						uni.login({ //请求微信登录接口，获取code
							provider: 'weixin',
							success: (res) => {
								var code = res.code;
								console.log(code)
								uni.getUserInfo({
									provider: 'weixin',
									success: (info) => {
										//这里请求登录接口，传code给后台
										console.log(info);
										var that = this
									    var nickName = info.userInfo.nickName
									    var icon = info.userInfo.avatarUrl
										console.log(nickName)
										uni.request({
											url:'http://112.74.86.165:8082/wechatLogin',
											method:'POST',
											data:{
												code:code,
												nickName:nickName,
												icon:icon
											},
											success(result) {
												console.log(result)
												var that = this
												 if(result.data.data.companyId == ''){
													 uni.showToast({
													 	title:'登录成功，请先编辑个人信息',
													 	icon:'none'
													 })
													 uni.navigateTo({
													 	url:'./edit'
													 })
												 }else{
													 var that = this
													 uni.setStorage({
													 	key:'token',
													 	data:{
													 		token: result.data.data.token,
													 		nickName:nickName,
													 		icon:icon,
													 		name: result.data.data.name,
													 		sex: result.data.data.sex,
													 		phone: result.data.data.phone,
													 		companyId: result.data.data.companyId,
													 	},
													 })
												 }
											},		
										})
									},
									fail: () => {
										uni.showToast({title:"微信登录授权失败",icon:"none"});
									}
								})
						
							},
							fail: () => {
								uni.showToast({title:"微信登录授权失败",icon:"none"});
							}
						})
							
						}else{
							uni.showToast({
								title: '请先安装微信或升级版本',
								icon:"none"
							});
						}
					  }
					});

				},
			
			// 获取企业号id
			getID(){
				var that = this
				uni.getStorage({
					key:'token',	
					success: function (res) {
						that.id = res.data.companyId
					},
					fail(result) {
						uni.showToast({
							title:'登录成功，请先编辑个人信息',
							icon:'none'
						})
						uni.navigateTo({
							url:'./edit'
						})
					}
				})
			},
			//编辑个人资料
			edit(){
				uni.navigateTo({
					url:'./edit'
				})
			},
			//跳转到审核中
			toReview(){
				uni.navigateTo({
					url:'./inReview/inReview'
				})
			},
			//跳转到待开会
			toMetting(){
				uni.navigateTo({
					url:'./toBeHold/toBeHold'
				})
			},
			//跳转到历史记录
			toHistory(){
				uni.navigateTo({
					url:'./history/history'
				})
			},
			//提交反馈
			toSfeedback(){
					uni.navigateTo({
						url:'./feedback/feedback'
					})
			}
		}
	}
